import React from 'react';
import styled from "styled-components"
import css from "./Users.module.css"

const Paginator = ({ userCount, pageSize, onPageChange, currentPage }) => {
	let pageCount = Math.ceil(userCount / pageSize)
	let pages = []
	for (let i = 1; i <= pageCount; i++) {
		pages.push(i)
	}
	return (
		<div style={{margin: "3px 0"}}>
			{/* {pages.map((page) => (
				<PageNumber
					key={page}
					onClick={() => onPageChange(page)}
					className={currentPage === page && css.selectedPage}
				>
					{page}
				</PageNumber>
			))} */}
			<div style={{textAlign: "center"}}>
				{currentPage>1 && <PageNumber onClick={()=>onPageChange(1)}>{"<<"}</PageNumber>}
				{currentPage>1 && <PageNumber onClick={()=>onPageChange(currentPage-1)}>{"<"}</PageNumber>}
				{currentPage>4 && <PageNumber onClick={()=>onPageChange(currentPage-4)}>{currentPage-4}</PageNumber>}
				{currentPage>3 && <PageNumber onClick={()=>onPageChange(currentPage-3)}>{currentPage-3}</PageNumber>}
				{currentPage>2 && <PageNumber onClick={()=>onPageChange(currentPage-2)}>{currentPage-2}</PageNumber>}
				{currentPage>1 && <PageNumber onClick={()=>onPageChange(currentPage-1)}>{currentPage-1}</PageNumber>}
				<span className={css.selectedPage}>{currentPage}</span>
				{currentPage<pageCount && <PageNumber onClick={()=>onPageChange(currentPage+1)}>{currentPage+1}</PageNumber>}
				{currentPage<pageCount-1 && <PageNumber onClick={()=>onPageChange(currentPage+2)}>{currentPage+2}</PageNumber>}
				{currentPage<pageCount-2 && <PageNumber onClick={()=>onPageChange(currentPage+3)}>{currentPage+3}</PageNumber>}
				{currentPage<pageCount-3 && <PageNumber onClick={()=>onPageChange(currentPage+4)}>{currentPage+4}</PageNumber>}
				{currentPage<pageCount && <PageNumber onClick={()=>onPageChange(currentPage+1)}>{">"}</PageNumber>}
				{currentPage<pageCount && <PageNumber onClick={()=>onPageChange(pageCount)}>{">>"}</PageNumber>}
			</div>
		</div>
	)
}

export default Paginator

const PageNumber = styled.span`
   margin: 5px;
   padding: 0 3px;
   cursor: pointer;
   border: 1px solid #83aae4;
`