import "./App.css"
import React, { Suspense } from 'react';
import { Route, withRouter } from "react-router-dom"
import LoginContainer from "./components/Login/LoginContainer"
import Music from "./components/Music/Music"
import Navbar from "./components/Navbar/Navbar"
import News from "./components/News/News"
import Settings from "./components/Settings/Settings"
import UsersContainer from "./components/Users/UsersContainer"
import HeaderContainer from "./components/Header/HeaderContainer"
import FontsTest from "./components/FontsTest"
import { initializeApp } from "./redux/appReducer";
import { connect } from "react-redux";
import { compose } from "redux";
import Preloader from "./components/Common/Preloader/Preloader";
import { BrowserRouter } from "react-router-dom";
import { Provider } from "react-redux";
import store from "./redux/store";
import withSuspense from "./HOC/withSuspense";
const ProfileContainer = React.lazy(() => import('./components/Profile/ProfileContainer'))
const DialogsContainer = React.lazy(()=> import('./components/Dialogs/DialogsContainer')) // react lazy loading

class App extends React.Component {
   componentDidMount() {
      this.props.initializeApp()
   }
   render() {
      return this.props.initialized ? (
         <div className="app-wrapper">
            <HeaderContainer />
            <Navbar />
            <div className="app-wrapper-content">
               <Route exact path="/" render={withSuspense(ProfileContainer)} />
               <Route path="/profile/:userId?" render={withSuspense(ProfileContainer)} />
               <Route path="/login" render={() => <LoginContainer />} />
               <Route path="/dialogs" render={withSuspense(DialogsContainer)} />
               <Route path="/users" render={() => <UsersContainer />} />
               <Route path="/news" render={() => <News />} />
               <Route path="/music" render={() => <Music />} />
               <Route path="/settings" render={() => <Settings />} />
               <Route path="/fonts" render={() => <FontsTest />} />
            </div>
         </div>
      ) : <Preloader />
   }
}

const mapStateToProps = (state) => {
   return {
      initialized: state.app.initialized
   }
}
// Если будут проблемы с роутом - обертаем withRouter (те компоненты где есть роутер)
let AppWithRouter = compose(
   withRouter,
   connect(mapStateToProps, { initializeApp }))(App)

const AppSamurai = (props) => {
   return (
      <BrowserRouter>
         <Provider store={store}>
            <AppWithRouter />
         </Provider>
      </BrowserRouter>
   )
}

export default AppSamurai